.hidden {
  display: none;
}

.breed-select {
  text-align: center;
  margin-bottom: 25px;
  padding: 10px;
}

body {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  display: flex;
}

.cat-info {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
  display: flex;
}

.cat-description {
  width: 500px;
  height: 250px;
  text-align: center;
  border: solid gray;
  border-radius: 20px;
  padding: 20px;
}

.Breed-message {
  margin: 0;
  padding: 0;
}

.blur-up {
  filter: blur(5px);
  transition: filter .4s;
}

.blur-up.lazyloaded {
  filter: blur();
}

img {
  border-style: none;
  border-radius: 10px;
  box-shadow: 3px 2px 10px #000;
}

.loader {
  color: #131212;
  margin-bottom: 20px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 34px;
  font-weight: bold;
  position: relative;
}

.loader:before {
  content: "";
  width: 25px;
  height: 20px;
  box-sizing: border-box;
  border: 5px solid #a4a5a7;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation .6s linear infinite;
  position: absolute;
  bottom: 8px;
  left: 21px;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

